











































import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Choose extends Vue {
  // @Prop() private msg!: string;
  private radio = "";
  public toRolePage() {
    if (this.radio == "1") {
      this.$router.push("./detective");
    } else {
      this.$router.push("./Mrx");
    }
  }
}
